/* Projects.css */

.projects-container {
    max-width: 800px;
    margin: auto;
  }
  
  h2 {
    font-size: 2rem;
    color: #333;
  }
  
  p {
    font-size: 1.1rem;
    margin-top: 1rem;
    color: #555;
  }
  
  .project-list {
    margin-top: 1.5rem;
    list-style: none;
    padding: 0;
  }
  
  .project-list li {
    font-size: 1.1rem;
    margin-bottom: 0.5rem;
  }
  