/* Contact.css */

.contact-container {
    max-width: 800px;
    margin: auto;
  }
  
  h2 {
    font-size: 2rem;
    color: #333;
  }
  
  p {
    font-size: 1.1rem;
    margin-top: 1rem;
    color: #555;
  }
  
  .contact-form {
    margin-top: 1.5rem;
    display: flex;
    flex-direction: column;
  }
  
  label {
    margin-top: 1rem;
    font-size: 1rem;
    color: #333;
  }
  
  input, textarea {
    padding: 0.5rem;
    font-size: 1rem;
    margin-top: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  button {
    margin-top: 1.5rem;
    padding: 0.7rem 1.5rem;
    font-size: 1rem;
    color: #ffffff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  